.logo {
    font-size: $logo-fz;
    font-weight: $logo-weight;
    font-family: $logo-font, sans-serif;
    color: $logo-color;
}

.header {
    position: absolute;
    z-index: 10;
    color: $textColor;
    z-index: 4;
    width: 100%;
    top: 0;
    left: 0;
    padding-top: $header-padding-top;
    transition: .5s;
    &.scrolled{
        background: #fff;
        position: fixed;
        width: 100%;
        padding-bottom: $header-padding-top;
        box-shadow: 0px 2px 30px 4px rgba($color: $textColor, $alpha: .2);
        .logo{
            color: $textColor;
        }
        span {
            background: $textColor;
            &::before,
            &::after{
                background: $textColor;
            }
        }
    }
    .container {
        display: flex;
        align-items: center;
        @media (max-width: $header-adaptive) {
            justify-content: space-between;
        }
    }
    // .logo{
    //     img{
    //         @include size(60px, 60px);
    //     }
    // }
    .menu {
        margin-left: 20%;
        @media (max-width: $header-adaptive) {
            position: fixed;
            margin-left: 0;
            width: 100%;
            height: 100vh;
            top: 0;
            left: 0;
            background: $primary;
            clip-path: circle(0px at top right);
            transition: clip-path 0.5s;
        }
    }

    .menu-item{
        @media (max-width: $header-adaptive) {
            margin-left: 0 !important;
            margin-bottom: 5vh;
            width: 100%;
            text-align: center;
        }
    }
    .menu-link {
        &_active{
            color: #C8C7C7;
            display: flex;
            align-items: center;
            &::before{
                content: '';
                display: inline-block;
                width: 25px;
                height: 1px;
                background: $primary;
                margin-right: 10px;
            }
        }
        @media (max-width: $header-adaptive) {
            font-size: 24px;
        }
        @media(max-width:$md3){
            color: $lightTextColor;
            display: inline-block;
            &::before{
                width: 100%;
                display: block;
                background: $lightTextColor;
            }
        }
    }
    
}

.menu-list {
    display: flex;
    font-weight: 500;
    .menu-item {
        position: relative;
        &:not(:first-child) {
            margin-left: $menu-item_margin;
        }
        .menu-link {
            color: $lightTextColor;
            text-transform: uppercase;
            transition: color 0.3s;
            @media (min-width: 992px) {
                &:hover {
                    color: #C8C7C7;
                }
            }
        }
    }
    @media (max-width: $header-adaptive) {
        flex-direction: column;
        height: 100%;
        justify-content: center;
    }
}

// menu mobile button
#menu-checkbox {
    display: none;
}

.menu-btn {
    position: relative;
    z-index: 4;
    width: 40px;
    display: none;
    span {
        position: absolute;
        top: 0;
        width: 100%;
        height: 2px;
        background: $burger;
        transition: transform 0.3s;
        &::before {
            content: "";
            display: block;
            position: absolute;
            top: -8px;
            width: 100%;
            height: 2px;
            background: $burger;
            transition: transform 0.3s, top 0.3s;
        }
        &::after {
            content: "";
            display: block;
            position: absolute;
            top: 8px;
            width: 100%;
            height: 2px;
            background: $burger;
            transition: background 0.3s, transform 0.3s, top 0.3s;
        }
    }
    @media (max-width: $header-adaptive) {
        display: block;
    }
}

// menu mobile working
#menu-checkbox:checked ~ .menu {
    clip-path: circle(1250px at top right);
}

#menu-checkbox:checked ~ .conect {
    top: 20px;
}

#menu-checkbox:checked ~ .menu-btn {
    span {
        &::before {
            top: 0;
        }
        &::after {
            transform: rotate(90deg);
            top: 0;
        }
        transform: rotate(45deg);
    }
}
