* {
    margin: 0;
    box-sizing: border-box;
}

.container {
    max-width: $container;
    margin: 0 auto;
    padding: 0 $containerPadding;
}

.wrapper {
    position: relative;
    overflow: hidden;
}

html {
    font-size: $defaultFontSize;
    @media(max-width:$md3){
        font-size: 16px;
    }
}

body {
    font-family: $font-family, sans-serif;
    color: $lightTextColor;
    font-size: 1rem;
    font-weight: $font-weight;
    transition: color 0.3s;
}

a {
    text-decoration: none;
}

ul {
    list-style: none;
    padding: 0;
}

h2,
h3 {
    font-size: $titles-fz;
    font-weight: $title-weight;
    color: $textColor;
    font-family: $title-font;
}

input,
textarea,
button {
    font-family: inherit;
}
