// базовые подключения
@import "vars";
@import "mixins";
@import "fonts";
@import "settings";

// global v2
.section-title {
    font-size: rem(20px);
    display: flex;
    align-items: center;
    @media (max-width: $md2) {
        position: relative;
        display: inline-block;
        width: 100%;
        text-align: center;
    }
    &::before {
        content: "";
        display: inline-block;
        width: 25px;
        height: 3px;
        background: $primary;
        margin-right: 10px;
        @media (max-width: $md2) {
            position: absolute;
            top: 40px;
            width: 20%;
            left: 50%;
            transform: translateX(-50%);
        }
    }
}

// global socials bem block
.socials {
    display: flex;
    &__item {
        padding: 0 30px;
    }
    &__link {
        text-transform: uppercase;
        color: $lightTextColor;
        transition: color 0.3s;
        @media (min-width: 992px) {
            &:hover {
                color: $primary;
            }
        }
    }
}

// global phone bem block
.phone {
    a {
        display: block;
        color: $lightTextColor;
        font-weight: 600;
        transition: color 0.3s;
        &:first-child {
            margin-bottom: 15px;
        }
        @media (min-width: 992px) {
            &:hover {
                color: $primary;
                svg {
                    fill: $primary;
                }
            }
        }
    }
    svg {
        width: 12px;
        height: 12px;
        fill: $lightTextColor;
        margin-right: 10px;
        transition: fill 0.3s;
    }
}

// подключения компонентов страницы
@import "./components/header";

.intro {
    height: 100vh;
    display: flex;
    background: url(../img/intro-bg.png) right bottom / contain no-repeat;
    background-color: $textColor;
    animation: intro-bg 5s ease-in-out infinite;
    &::before {
        @include pseudo(0, 0);
        width: 30%;
        height: 100vh;
        background: #313131;
        @media (max-width: $md3) {
            display: none;
        }
    }
    .container {
        position: relative;
        z-index: 4;
        width: 100%;
    }
    &__title {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        @media (max-width: $md3) {
            align-items: flex-start;
            justify-content: center;
            padding-top: 75px;
        }
    }
    h1 {
        font-size: rem(90px);
        letter-spacing: 0.06em;
        color: #fff;
        padding-left: 15%;
        text-transform: uppercase;
        @media(min-width:$md1){
            position: relative;
            animation: br .1s 7.5s forwards;
        }
     
        @media (max-width: 992px) {
            font-size: rem(60px);
        }
        @media (max-width: $md3) {
            padding-left: 0;
            font-size: rem(40px);
            text-align: center;
        }

        @media (max-width: $md4) {
            font-size: rem(32px);
        }
        @media(min-width:$md1){
            &::after {
                content: "|";
                font-weight: 300;
                position: absolute;
                right: 0;
                width: 82%;
                color: $primary;
                background: $textColor;
                animation: typing 3.5s steps(12) forwards, enter 2s  steps(7) 4s forwards, hide 1s 7.1s forwards;
                
            }
        }
    }

    &__socials {
        position: absolute;
        top: 50%;
        left: -150px;
        transform: rotate(-90deg);
        @media (max-width: $md3) {
            display: none;
        }
    }
    &__phone {
        position: absolute;
        bottom: 75px;
        left: 15px;
        @media (max-width: $md3) {
            display: none;
        }
    }
    p {
        position: absolute;
        left: 35%;
        bottom: 75px;
        width: 206px;
        text-transform: uppercase;
        font-weight: 300;
        line-height: 1.45;
        @media (max-width: $md3) {
            left: 15px;
        }
    }
    &__pagination {
        display: flex;
        flex-direction: column;
        position: absolute;
        z-index: 10;
        top: 130px;
        right: 15px;
        transition: 0.3s;
        &.scrolled {
            position: fixed;
            top: 175px;
            background: #fff;
            padding: 10px;
            border-radius: 20px;
            a {
                color: $textColor;
            }
        }
        a {
            display: flex;
            align-items: center;
            color: $primary;
        }
        .line {
            display: inline-block;
            width: 24px;
            height: 1px;
            background: $lightTextColor;
            margin-left: 5px;
            transition: width 0.3s;
        }
        .number {
            position: relative;
            left: -30px;
            transition: left 0.3s;
        }
        li {
            margin-left: auto;
            overflow: hidden;
            &.active {
                .line {
                    width: 33px;
                }

                .number {
                    left: 0;
                }
            }
        }
        @media (max-width: $md3) {
            top: 30%;
        }
        .prev {
            display: none;
            position: absolute;
            width: 10px;
            height: 10px;
            border: none;
            border-top: 1px solid $lightTextColor;
            border-left: 1px solid $lightTextColor;
            top: -20px;
            left: 66%;
            transform: rotate(45deg);
            background: none;
            padding: 0;
            @media (max-width: $md3) {
                display: block;
            }
        }
        .next {
            display: none;
            position: absolute;
            width: 10px;
            height: 10px;
            border: none;
            border-top: 1px solid $lightTextColor;
            border-left: 1px solid $lightTextColor;
            bottom: -20px;
            left: 67%;
            transform: rotate(-135deg);
            background: none;
            padding: 0;
            @media (max-width: $md3) {
                display: block;
            }
        }
    }
    @media (max-width: $md3) {
        background-image: url("../img/intro-bg-small.png");
        background-size: 130% auto;
    }
}

// interesting
.cols {
    display: flex;
    gap: 75px;
    @media (max-width: $md1) {
        flex-direction: column;
        gap: 40px;
    }
}

.main-col {
    flex: 0 1 25%;
    @media (max-width: $md1) {
        padding: 0 5%;
        flex: 0 1 100%;
    }
    // padding-right: 38px;
    .links {
        display: flex;
        gap: 30px;
        margin-top: 35px;
        a {
            font-size: rem(18px);
            color: $textColor;
            &::after {
                content: "";
                display: block;
                width: 100%;
                height: 1px;
                background: $primary;
                margin-top: 6px;
                transition: width 0.3s;
            }
            @media (min-width: $md1) {
                &:hover::after {
                    width: 0;
                }
            }
        }
    }
}

.text-col {
    flex: 0 1 25%;
    // padding-left: 38px;
    @media (max-width: $md1) {
        flex: 0 1 100%;
    }
}

.img-col {
    flex: 0 1 50%;
    overflow: hidden;
    &__img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: transform 1s;
        @media (min-width: $md1) {
            &:hover {
                transform: scale(1.08);
            }
        }
    }
    @media (max-width: $md1) {
        flex: 0 1 100%;
        position: relative;
        width: 100vw;
        left: -20px;
    }
}
.interesting {
    padding: $sectionPadding 0;
    h2 {
        margin-bottom: 35px;
    }
    .main-col {
        color: $textColor;
        &__text {
            font-size: rem(18px);
            font-weight: 400;
            &_first {
                margin-bottom: 25px;
            }
        }
    }
    .text-col {
        counter-reset: counter;
        h3 {
            font-size: rem(18px);
            font-weight: 500;
            margin-bottom: 15px;
            position: relative;
            &::before {
                counter-increment: counter;
                content: "0" counter(counter);
                position: absolute;
                top: 0;
                left: -25px;
                font-size: rem(18px);
                font-weight: 400;
                color: $primary;
                @media (max-width: $md1) {
                    left: -40px;
                }
            }
        }
        &__text {
            font-size: rem(18px);
            color: $textColor;
        }
        &__item {
            margin-bottom: 20px;
            @media (max-width: $md1) {
                padding-left: 45px;
            }
        }
    }
}

// project
.projects {
    padding: $sectionPadding 0;
    background: $sectionBg;
    .section-title {
        margin-bottom: 40px;
    }
    .cols {
        @media (max-width: $md1) {
            margin-bottom: 65px;
            &:last-child {
                margin-bottom: 0;
            }
        }
    }
    .main-col {
        padding: 45px 0;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        h3 {
            font-weight: 500;
            font-size: rem(20px);
            line-height: 1.2;
            letter-spacing: 0.03em;
            text-transform: uppercase;
        }
        @media (max-width: $md1) {
            padding: 25px 0;
        }
    }
    .text-col {
        padding-top: 45px;
        &__item {
            margin-bottom: 15px;
        }
        &__text {
            color: $textColor;
            display: flex;
            justify-content: space-between;
            font-size: rem(18px);
            span:nth-child(2) {
                font-weight: 400;
            }
        }
        @media (max-width: $md1) {
            padding-left: 10%;
            padding-right: 10%;
            padding-top: 0;
        }
    }
}

// calc section
.calc {
    .container {
        max-width: 100%;
        display: flex;
        padding: 0;
        @media (max-width: $md1) {
            flex-direction: column;
        }
    }
    .calc-block {
        position: relative;
        width: 55%;
        padding: 75px 60px;
        @include image-set("../img/img_5");
        @include bg-cover;
        color: #fff;
        .section-title {
            color: #fff;
            margin-bottom: 50px;
        }
        &::before {
            content: "";
            display: block;
            position: absolute;
            width: 100%;
            height: 100%;
            background: #000;
            opacity: 0.9;
            top: 0;
            left: 0;
        }
        &__content {
            width: 100%;
            position: relative;
            z-index: 2;
        }
        @media (max-width: 1240px) {
            padding: 75px 30px;
        }
        @media (max-width: 1024px) {
            width: 100%;
            background: $textColor;
            &::before {
                display: none;
            }
        }
    }
    .calc__img {
        width: 45%;
        overflow: hidden;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: transform 1s;
            @media (min-width: $md1) {
                &:hover {
                    transform: scale(1.08);
                }
            }
        }
        @media (max-width: $md1) {
            display: none;
        }
    }

    .calc-block__form {
        display: flex;
        label {
            display: block;
            margin-bottom: 30px;
        }
        input[type="range"] {
            width: 100%;
            -webkit-appearance: none;
            -moz-appearance: none;
            appearance: none;
            padding: 9px 15px;
            background: transparent;
        }
        input[type="range"]::-webkit-slider-runnable-track {
            height: 1px;
            background-color: $primary;
        }
        input[type="range"]::-webkit-slider-thumb {
            cursor: pointer;
            width: 15px;
            height: 15px;
            -webkit-appearance: none;
            border-radius: 50%;
            background: #fff;
            margin: -7px;
        }

        input[type="range"]::-moz-range-track {
            border-radius: 10px/100%;
            height: 5px;
            border: 1px solid cyan;
            background-color: #fff;
        }
        input[type="range"]::-moz-range-thumb {
            background: #ecf0f1;
            border: 1px solid cyan;
            border-radius: 10px/100%;
            cursor: pointer;
        }
        input[type="submit"] {
            display: block;
            width: 100%;
            background: $primary;
            border: none;
            padding: 15px;
            text-transform: uppercase;
            font-size: rem(15px);
            font-weight: 400;
            transition: background 0.3s;
            @media (min-width: 992px) {
                &:hover {
                    background: darken($primary, 15%);
                }
            }
        }
        input[type="reset"] {
            display: inline-block;
            color: #fff;
            background: none;
            border: none;
            font-size: rem(15px);
            font-weight: 400;
        }
        .reset {
            margin-top: 22px;
            display: inline-block;
            &::after {
                content: "";
                display: block;
                width: 100%;
                height: 1px;
                background: $primary;
                margin-top: 5px;
                transition: width 0.3s;
            }
            @media (min-width: 992px) {
                &:hover::after {
                    width: 0;
                }
            }
            @media (max-width: $md3) {
                display: none;
            }
        }
        @media (max-width: $md3) {
            flex-direction: column-reverse;
        }
    }

    .calc-block__column:first-child {
        padding-right: 15px;
    }
    .calc-block__column:last-child {
        padding-left: 15px;
    }

    .calc-block__column {
        flex: 0 1 50%;
        @media (max-width: $md3) {
            flex: 0 1 100%;
            padding: 0 !important;
        }
    }

    .select2 {
        display: block;
        width: 100% !important;
        background: transparent;
        color: #fff;
        margin-bottom: 40px;
    }

    .select2-selection--single {
        border: 1px solid $primary !important;
        background: transparent !important;
        border-radius: 0 !important;
        height: auto !important;
        font-size: rem(20px);
        font-weight: 400;
        &[aria-expanded="true"] {
            .select2-selection__arrow {
                transform: rotate(45deg) translateY(10px);
            }
        }
    }

    .select2-selection__rendered {
        color: #fff !important;
        padding-top: 13px;
        padding-bottom: 13px;
        padding-left: 35px !important;
    }
    .select2-selection__arrow {
        position: absolute !important;
        top: 15px !important;
        right: 10% !important;
        width: 20px !important;
        height: 20px !important;
        border-top: 2px solid #fff;
        border-left: 2px solid #fff;
        transform: rotate(-135deg);
        transition: transform 0.3s;
        b {
            display: none !important;
        }
    }

    .range-body {
        position: relative;
        display: flex;
        align-items: center;
        margin-bottom: 75px;
        .current {
            position: absolute;
            top: 20px;
        }
    }
    .range-body_2 {
        .current {
            transform: translateX(-100%);
        }
    }
}

// select 2
.select2-results__options {
    background: $primary;
    color: #000;
    font-weight: 400;
    font-size: rem(16px);
}
.select2-container {
    .select2-dropdown {
        transform-origin: top;
        animation: select 0.3s 1 forwards;
    }
}
@keyframes select {
    0% {
        transform: scaleY(0);
    }
    100% {
        transform: scaleY(1);
    }
}

// footer
.footer {
    padding: 70px 0;
    background: $textColor;
    .container {
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media (max-width: $md1) {
            flex-direction: column;
            align-items: flex-start;
        }
    }
    .menu {
        margin-left: 0;
        &-list {
            @media (max-width: $md1) {
                flex-direction: column;
                padding-top: 25px;
            }
        }
        &-item {
            @media (max-width: $md1) {
                margin-left: 0 !important;
                margin-bottom: 25px;
                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
        &-link {
            @media (max-width: $md1) {
                color: #c8c7c7 !important;
            }
        }
    }
    .phone {
        @media (max-width: $md1) {
            align-self: flex-end;
        }
    }

    .socials {
        &__item {
            padding: 0;
            margin-left: 60px;
            &:first-child {
                margin-left: 0;
            }
            @media (max-width: 1056px) {
                margin-left: 30px;
                &:first-child {
                    margin-left: 0;
                }
            }
        }
        @media (max-width: $md1) {
            align-self: flex-end;
            margin-top: -125px;
            padding-bottom: 45px;
        }
        @media (max-width: $md4) {
            flex-direction: column;
            padding-bottom: 35px;
            margin-top: -200px;
            &__item {
                margin-bottom: 20px;
                margin-left: 0;
                text-align: center;
            }
        }
    }
    @media (max-width: $md1) {
        border-top: 1px solid #6a6a6a;
    }
}
// scrolbar

::-webkit-scrollbar {
    width: 20px;
}

::-webkit-scrollbar-track {
    background: #fff;
}

::-webkit-scrollbar-thumb {
    padding: 10px;
    background: #313131;
    border-radius: 20px;
    border: 5px solid #fff;
}

// keyframe

@keyframes intro-bg {
    0% {
        background-position-y: 0%;
    }
    50% {
        background-position-y: 25px;
    }
    100% {
        background-position-y: 0%;
    }
}

// Animation
@keyframes typing {
	to { width: 0 }
}

@keyframes enter {
	to{
        right: 415px;
    }
}

@keyframes hide{
	to{
        opacity: 0;
    }
}


@keyframes br {

	to{
        padding-right: 20%;
    }
}
